.modals {
	display: none;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	z-index: 999;
	transition: 0.3s;
	overflow: hidden; }
.modal {
	width: 320px;
	padding: 24px 16px 16px 16px;
	background: #ffffff;
	z-index: 999;
	position: absolute;
	bottom: 10px;
	left: 50%;
	transform: translate(-50%, 150%);
	transition: 0.3s;
	&__close {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 32px;
		height: 32px;
		border-radius: 50%;
		background: #ffffff;
		position: absolute;
		top: -16px;
		left: 50%;
		transform: translateX(-50%); }
	&__title {
		display: block;
		font-family: $RM;
		font-size: 17px;
		line-height: 20px;
		text-align: center;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: $text-black;
		margin-bottom: 4px; }
	&__subtitle {
		display: block;
		font-family: $RM;
		font-size: 13px;
		line-height: 16px;
		text-align: center;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: $text-black; }
	input {
		width: 100%;
		font-family: $RM;
		font-size: 22px;
		line-height: 24px;
		color: $text-black;
		font-feature-settings: 'pnum' on, 'lnum' on;
		border-radius: 8px;
