		padding: 12px 16px;
		text-align: center;
		margin: 24px 0;
		outline: none;
		&:focus {
			border: 1px solid #263238;
			color: #263238; } }
	p {
		font-family: $RB;
		font-size: 10px;
		line-height: 16px;
		letter-spacing: 1px;
		text-transform: uppercase;
		text-align: center;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: $red; }
	&__cost {
		display: block;
		font-family: $RB;
		font-size: 32px;
		line-height: 36px;
		text-align: center;
		font-feature-settings: 'pnum' on, 'lnum' on;
		color: #454B52; }
	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: $RB;
		font-size: 17px;
		line-height: 16px;
		color: #FFFFFF;
		background: $red;
		padding: 20px 16px;
		border-radius: 8px;
		text-decoration: none;
		margin-top: 18px;
		transition: 0.2s; } }
.disabled-btn {
