		display: none;
		&:checked + label {
			&:before {
				border: 2px solid #2183D4; }
			&:after {
				display: block; } } }
	label {
		font-family: $RM;
		font-size: 15px;
		line-height: 20px;
		color: $text-black;
		padding-left: 32px;
		cursor: pointer;
		position: relative;
		&:before {
			position: absolute;
			content: '';
			width: 24px;
			height: 24px;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			border: 1px solid rgba(0, 0, 0, 0.2);
			border-radius: 50%;
			box-sizing: border-box; }
		&:after {
			display: none;
			position: absolute;
			content: '';
			top: 50%;
			left: 5px;
			transform: translateY(-50%);
			width: 14px;
			height: 14px;
			background: #2183D4;
			border-radius: 50%; }
		&:hover:before {
			border: 2px solid #2183D4; } } }
